"use client";

import { format, differenceInDays, parseISO } from "date-fns";
import { useState, useEffect, useRef } from "react";

interface Trip {
  id: string;
  title: string;
  destination: string;
  start_date: string;
  end_date: string;
  description?: string;
  created_at: string;
  cover_image?: string;
}

interface TripCardProps {
  trip: Trip;
  onDelete?: (tripId: string) => void;
  onEdit?: (tripId: string) => void;
}

export default function TripCard({
  trip,
  onDelete,
  onEdit,
}: TripCardProps) {
  const [showDropdown, setShowDropdown] = useState(false);
  const dropdownRef = useRef<HTMLDivElement>(null);
  const startDate = parseISO(trip.start_date);
  const endDate = parseISO(trip.end_date);
  const duration = differenceInDays(endDate, startDate) + 1;
  const isUpcoming = startDate > new Date();
  const isOngoing = startDate <= new Date() && endDate >= new Date();

  // Close dropdown when clicking outside
  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target as Node)
      ) {
        setShowDropdown(false);
      }
    }

    if (showDropdown) {
      document.addEventListener("mousedown", handleClickOutside);
      return () =>
        document.removeEventListener("mousedown", handleClickOutside);
    }
  }, [showDropdown]);

  const getStatusBadge = () => {
    if (isOngoing) {
      return (
        <span className="inline-flex items-center px-3 py-1 rounded-sm text-xs font-medium bg-[#C4C5BA] text-[#595f39]">
          <div className="w-1.5 h-1.5 bg-[#595f39] rounded-full mr-1.5 animate-pulse"></div>
          Ongoing
        </span>
      );
    } else if (isUpcoming) {
      return (
        <span className="inline-flex items-center px-3 py-1 rounded-sm text-xs font-medium bg-[#C4C5BA] text-[#595f39]">
          <svg
            className="w-3 h-3 mr-1"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"
            />
          </svg>
          Upcoming
        </span>
      );
    } else {
      return (
        <span className="inline-flex items-center px-3 py-1 rounded-sm text-xs font-medium bg-[#C4C5BA] text-[#1B1B1B]">
          <svg
            className="w-3 h-3 mr-1"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M5 13l4 4L19 7"
            />
          </svg>
          Completed
        </span>
      );
    }
  };

  return (
    <div className="bg-[#E4E4DE] border border-[#C4C5BA] rounded-sm shadow-sm hover:shadow-md transition-all duration-300 transform hover:-translate-y-1 group">
      {/* Card Header with Cover Image */}
      <div className="h-48 bg-gradient-to-br from-[#595f39] via-[#C4C5BA] to-[#1B1B1B] relative overflow-hidden">
        {trip.cover_image ? (
          <img
            src={trip.cover_image}
            alt={trip.title}
            className="w-full h-full object-cover"
          />
        ) : null}
        <div className="absolute inset-0 bg-black/20"></div>
        <div className="absolute top-4 left-4 right-4 flex justify-between items-start">
          {getStatusBadge()}
          <button className="p-2 bg-white/20 backdrop-blur-sm rounded-full hover:bg-white/30 transition-colors">
            <svg
              className="w-4 h-4 text-white"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
              />
            </svg>
          </button>
        </div>
        <div className="absolute bottom-4 left-4 right-4">
          <h3 className="text-xl font-bold text-white mb-1 group-hover:text-yellow-200 transition-colors">
            {trip.title}
          </h3>
          <div className="flex items-center text-white/90">
            <svg
              className="w-4 h-4 mr-1"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"
              />
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"
              />
            </svg>
            <span className="text-sm">{trip.destination}</span>
          </div>
        </div>
      </div>

      {/* Card Content */}
      <div className="p-6 bg-[#E4E4DE]">
        {trip.description && (
          <p className="text-[#595f39] text-sm mb-4 line-clamp-2">
            {trip.description}
          </p>
        )}

        {/* Trip Details */}
        <div className="space-y-3 mb-6">
          <div className="flex items-center justify-between text-sm">
            <div className="flex items-center text-[#595f39]">
              <svg
                className="w-4 h-4 mr-2"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z"
                />
              </svg>
              <span>Duration</span>
            </div>
            <span className="font-medium text-[#1B1B1B]">
              {duration} {duration === 1 ? "day" : "days"}
            </span>
          </div>

          <div className="flex items-center justify-between text-sm">
            <div className="flex items-center text-[#595f39]">
              <svg
                className="w-4 h-4 mr-2"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
              <span>Dates</span>
            </div>
            <span className="font-medium text-[#1B1B1B]">
              {format(startDate, "MMM d")} - {format(endDate, "MMM d, yyyy")}
            </span>
          </div>
        </div>

        {/* Action Buttons */}
        <div className="flex space-x-3">
          <a
            href={`/itinerary/${trip.id}`}
            className="flex-1 bg-[#595f39] text-[#E4E4DE] py-2.5 px-4 rounded-sm font-medium hover:bg-[#1B1B1B] focus:outline-none focus:ring-2 focus:ring-[#595f39] focus:ring-offset-2 transition-all duration-200 shadow-sm hover:shadow-md transform hover:-translate-y-0.5 text-center tracking-wide"
          >
            View Itinerary
          </a>
          <div className="relative" ref={dropdownRef}>
            <button
              onClick={() => setShowDropdown(!showDropdown)}
              className="p-2.5 bg-[#C4C5BA] hover:bg-[#595f39] rounded-sm transition-colors focus:outline-none focus:ring-2 focus:ring-[#595f39] focus:ring-offset-2"
            >
              <svg
                className="w-5 h-5 text-gray-600"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z"
                />
              </svg>
            </button>

            {showDropdown && (
              <div className="absolute right-0 top-full mt-2 w-48 bg-[#E4E4DE] rounded-sm shadow-md border border-[#C4C5BA] py-1 z-10">
                <button
                  onClick={() => {
                    setShowDropdown(false);
                    if (onEdit) onEdit(trip.id);
                  }}
                  className="w-full text-left px-4 py-2 text-sm text-[#1B1B1B] hover:bg-[#C4C5BA]/50 flex items-center transition-colors"
                >
                  <svg
                    className="w-4 h-4 mr-2 text-[#595f39]"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z"
                    />
                  </svg>
                  Edit Trip
                </button>
                {onDelete && (
                  <button
                    onClick={() => {
                      setShowDropdown(false);
                      if (
                        window.confirm(
                          `Are you sure you want to delete "${trip.title}"? This action cannot be undone.`
                        )
                      ) {
                        onDelete(trip.id);
                      }
                    }}
                    className="w-full text-left px-4 py-2 text-sm text-[#1B1B1B] hover:bg-[#C4C5BA]/50 flex items-center transition-colors"
                  >
                    <svg
                      className="w-4 h-4 mr-2 text-[#1B1B1B]"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                      />
                    </svg>
                    Delete Trip
                  </button>
                )}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
